import Service from '@/service/art-cycle';
import { emitter } from '@/event-bus'

const artCycle = {
    namespaced: true,
    state: () => ({
        cyclesSummary: [],
        patient: null,
        partner: null,
        cycleTypes: null,
        selectedCycle: null,
        cycleTypeOptions: null,
        spermSourceOptions: null,
        eggSourceOptions: null
    }),
    mutations: {
    },
    actions: {
        getCyclesDetails({ state, commit }, demographicNo) {
            Service.getCycleSummaryDetail(demographicNo).then((resp) => {
                let {
                    petientDetails: _patientDetails,
                    partnerDetails: _partnerDetails,
                    cyclesSummery,
                    cycleTypes: _cycleTypes,
                } = resp.data.data;

                state.cyclesSummary = cyclesSummery;
                state.patient = _patientDetails;
                state.partner = _partnerDetails;
                state.cycleTypes = _cycleTypes;
                emitter.emit("artCycleStore:detail-loaded", cyclesSummery);
            });
        },
        updateSelectedCycle({ state }, cycle) {
            state.selectedCycle = cycle;
        },
        updateCycle({ state }, cycle) {
            let index = state.cyclesSummary.findIndex(item => item.cycleId === cycle.cycleId);
            Object.assign(state.cyclesSummary[index], {
                ...cycle,
                cycleCompleted: cycle.complete
            })
        },
        getCycleFormFieldOptions({ state }) {
            if (!state.cycleTypeOptions || !state.spermSourceOptions || !state.eggSourceOptions) {
                Service.getFormFieldOptions().then((resp) => {
                    state.cycleTypeOptions = resp.data.cycleTypes;
                    state.spermSourceOptions = resp.data.spermSources;
                    state.eggSourceOptions = resp.data.eggSource;
                });
            }
        }
    }
}

export {
    artCycle
}